import React, { useState, createContext,useEffect } from 'react'
import "./Home.css"
import Body from "./Body"
const SetCountry = createContext();
const Home = () => {
    const [country, setCountry] = useState("Pakistan");
    const [countriesList, setCountriesList] = useState(new Array([]));
    useEffect(() => {
        FetchCountries();
    }, [country]);
    async function FetchCountries() {
        const CountriesUrl = "https://restcountries.eu/rest/v2/all";
        const response = await fetch(CountriesUrl);
        let data = await response.json();
        setCountriesList(data);
    }
    return (
        <div>
            <div className="heading">
                <h1>
                    <strong><i className="fas fa-virus"></i> CoronaVirus Cases </strong>
                </h1>
            </div>
            <div className="select-country">
                <div className="select">
                    <select value={country} onChange={(e) => setCountry(e.target.value)} >
                        {countriesList.map((c, index) => (
                            <option key={index} value={c.name}>{c.name}</option>
                        ))}
                    </select>
                </div>
            </div>
            <SetCountry.Provider value={country}>
                <Body/>
            </SetCountry.Provider>
        </div >
    )
}
export default Home;
export { SetCountry };